import Avatar from "@mui/material/Avatar";
import CssBaseline from "@mui/material/CssBaseline";
import FormControlLabel from "@mui/material/FormControlLabel";
import Typography from "@mui/material/Typography";
import LockOutlinedIcon from "@mui/icons-material/LockOutlined";
import Button from "@mui/material/Button";
import { Card, Checkbox, Grid, TextField } from "@mui/material";
import { Box, styled, useTheme } from "@mui/material";
import useAuth from "app/hooks/useAuth";
import { Formik } from "formik";
import { useState } from "react";
import { NavLink, useNavigate } from "react-router-dom";
import * as Yup from "yup";
import Container from "@mui/material/Container";
import Alert from "@mui/material/Alert";

const FlexBox = styled(Box)(() => ({ display: "flex", alignItems: "center" }));

const JustifyBox = styled(FlexBox)(() => ({ justifyContent: "center" }));

export const InfoAlert = ({ severity, message }) => {
  return <Alert severity={severity}>{message}</Alert>;
};

export const ContentBox = styled(Box)(() => ({
  height: "100%",
  padding: "32px",
  position: "relative",
  background: "rgba(0, 0, 0, 0.01)",
}));

const JWTRoot = styled(JustifyBox)(() => ({
  background: "#1A2038",
  minHeight: "100% !important",
  "& .card": {
    maxWidth: 800,
    minHeight: 400,
    margin: "1rem",
    display: "flex",
    borderRadius: 12,
    alignItems: "center",
  },
}));

// inital login credentials
const initialValues = {
  email: "",
  password: "",
  remember: true,
};

// form field validation schema
const validationSchema = Yup.object().shape({
  password: Yup.string()
    .min(6, "Password must be 6 character length")
    .required("Password is required!"),
  email: Yup.string()
    .email("Invalid Email address")
    .required("Email is required!"),
});

const JwtLogin = () => {
  const theme = useTheme();
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const [alertmessage, setAlertmessage] = useState("");
  const [alertSeverity, setAlertseverity] = useState("");

  const { login } = useAuth();

  const handleFormSubmit = async (values) => {
    setLoading(true);
    try {
      await login(values.email, values.password);
      setTimeout(() => {
        setAlertmessage("");
      }, 3000);
      navigate("/");
    } catch (e) {
      if (e.massage === "user_not_found") {
        setAlertmessage("Account not found. Please sign up.");
        setAlertseverity("error");
      } else {
        setAlertmessage("Incorrect email or password. Please try again.");
        setAlertseverity("error");
      }
      setTimeout(() => {
        setAlertmessage("");
      }, 2000);
    } finally {
      setLoading(false);
    }
  };

  return (
    <Card
      className="card"
      sx={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        backgroundImage: `url(${"/assets/images/illustrations/transparant.PNG"})`,
        backgroundColor: "#263238",
        backgroundSize: "cover",
        height: "100vh",
        justifyContent: "center",
      }}
    >
      <Card
        marginY={"100px"}
        backgroundColor="white"
        padding={"2"}
        sx={{
          border: "3px solid",
          borderColor: "#cfd8dc",
          marginRight: "5%",
          maxWidth: "460px",
          marginBottom: "7%",
        }}
      >
        <CssBaseline />
        <Container component="main" maxWidth="xs">
          <Box
            sx={{
              marginTop: 3,
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
            }}
          >
            {alertmessage && (
              <InfoAlert
                severity={alertSeverity === "success" ? "success" : "error"}
                message={alertmessage}
              />
            )}
            <Avatar sx={{ m: 1, bgcolor: "secondary.main" }}>
              <LockOutlinedIcon />
            </Avatar>
            <Typography component="h1" variant="h5">
              Sign in
            </Typography>
            <ContentBox>
              <Formik
                sx={{ mt: 1 }}
                onSubmit={handleFormSubmit}
                initialValues={initialValues}
                validationSchema={validationSchema}
              >
                {({
                  values,
                  errors,
                  touched,
                  handleChange,
                  handleBlur,
                  handleSubmit,
                }) => (
                  <form onSubmit={handleSubmit}>
                    <TextField
                      fullWidth
                      size="small"
                      type="email"
                      name="email"
                      label="Email"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.email}
                      onChange={handleChange}
                      helperText={touched.email && errors.email}
                      error={Boolean(errors.email && touched.email)}
                      sx={{ mb: 3 }}
                    />

                    <TextField
                      fullWidth
                      size="small"
                      name="password"
                      type="password"
                      label="Password"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.password}
                      onChange={handleChange}
                      helperText={touched.password && errors.password}
                      error={Boolean(errors.password && touched.password)}
                      sx={{ mb: 1.5 }}
                    />
                    <FormControlLabel
                      control={<Checkbox value="remember" color="primary" />}
                      label="Remember me"
                    />
                    <Button
                      type="submit"
                      fullWidth
                      variant="contained"
                      sx={{ mt: 3, mb: 3 }}
                    >
                      Sign In
                    </Button>
                    <Grid container>
                      <Grid item xs>
                        <NavLink
                          to="/session/forgot-password"
                          style={{ color: theme.palette.primary.main }}
                        >
                          Forgot password?
                        </NavLink>
                      </Grid>
                      {/* <Grid item>
                        <NavLink
                          to="/session/signup"
                          style={{
                            color: theme.palette.primary.main,
                            marginLeft: 5,
                          }}
                        >
                          {"Don't have an account? Sign Up"}
                        </NavLink>
                      </Grid> */}
                    </Grid>
                  </form>
                )}
              </Formik>{" "}
            </ContentBox>{" "}
          </Box>
        </Container>{" "}
      </Card>
    </Card>
  );
};

export default JwtLogin;
